.footer__info {
    padding-bottom: 60px;
    max-width: 880px;
    margin: 0 auto;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
}
@media screen and (max-width: 580px) {
    .footer__info {
        max-width: 282px;
        padding-bottom: 35px;
        font-size: 14px;
        line-height: 17px;
    }
}