.profile {
    max-width: 880px;
    margin: 0 auto;
    padding-top: 35px;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
}
@media screen and (max-width: 580px) {
    .profile {
        max-width: 282px;
        padding-top: 40px;
        grid-template-columns: 1fr;
    }
}